@use '../base/variables' as *;
@use '../base/mixins' as *;
@use 'sass:math';
@use 'sass:color';

@include tablet {
    .seccion-inferior {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog {
    margin-bottom: 2rem;

    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.texto-entrada {
    a {
        color: $negro;
        margin-top: 3rem;
        display: block;
        @include tablet {
            margin-top: 0;
        }
    }
    h4 {
        margin: 0;
        line-height: 1.4;

        &::after {
            content: '';
            display:block;
            width: 15rem;
            height: 0.3rem;
            background-color: $verde;
        }
    }
    span {
        color: $amarillo;
    }
}